/* eslint-disable react/no-array-index-key */
import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { v4 as uuidv4 } from 'uuid';
import {
  Box,
  Button,
  Container,
  Card,
  Typography,
  TextField,
  makeStyles
} from '@material-ui/core';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import { HEADING_OPTIONS } from 'src/constants';

const useStyles = makeStyles({
  root: {
    padding: 20,
  },
  container: {

  },
  headingContent: {
    height: 300,
  },
  labelTitle: {
    marginRight: 10,
  },
  fieldTitle: {
    marginRight: 10,
  },
  panel: {
    height: 500,
  },
  treeView: {

  },
  treeItem: {
    witdh: 300,
  }
});

function HeadingDetail({ data, updateData, removeData, }) {
  const classes = useStyles();
  const [saving, setSaving] = useState(false);
  const [heading, setHeading] = useState(data);
  const [field, setField] = useState(null);
  const [value, setValue] = useState('');

  useEffect(() => {
    setHeading(data);
    if (data && data.field) {
      HEADING_OPTIONS.some((item) => {
        if (item.name === data.field) {
          setField(item);
        }
      });
    }

    if (data && data.value) {
      setValue(data.value);
    } else if (data && !data.value) {
      setValue('');
    }
  }, [data]);

  if (!data) {
    return null;
  }

  const handleChangeHeadingLabel = (event) => {
    if (value !== '') {
      setHeading({ ...heading, label: event.target.value, value });
    } else {
      setHeading({ ...heading, label: event.target.value });
    }
  };

  const handleSave = () => {
    setSaving(false);
    if (value !== '') {
      updateData({ ...heading, field: field.name, value });
    } else {
      updateData({ ...heading, field: field.name });
    }
  };

  const newSubHeading = () => {
    const id = uuidv4();
    const subHeading = { id, label: 'New SubHeading', field: 'label' };
    if (heading && heading.children) {
      updateData({ ...heading, children: [...heading.children, subHeading] });
    } else {
      updateData({ ...heading, children: [subHeading] });
    }
  };

  return (
    <Box
      className={classes.root}
    >
      {heading && (
      <Container
        className={classes.container}
      >
        <Box
          className={classes.headingContent}
        >
          <Box
            mt={3}
            display="flex"
          >
            <Typography
              className={classes.labelTitle}
              color="textPrimary"
              variant="h4"
            >
              Label:
            </Typography>
            {saving
              ? (
                <TextField
                  onChange={(e) => { handleChangeHeadingLabel(e); }}
                  value={heading.label}
                />
              ) : (
                <Typography
                  color="textPrimary"
                  variant="h5"
                >
                  {heading.label}
                </Typography>
              )}
          </Box>
          <Box
            mt={3}
            display="flex"
            width={300}
          >
            <Typography
              color="textPrimary"
              variant="h4"
              className={classes.fieldTitle}
            >
              Field:
            </Typography>
            {saving
              ? (
                <Autocomplete
                  id="grouped-demo"
                  value={field}
                  options={HEADING_OPTIONS}
                  groupBy={(option) => option.group}
                  getOptionLabel={(option) => option.title}
                  style={{ width: 300 }}
                  renderInput={(params) => (<TextField {...params} label="With categories" variant="outlined" />)}
                  onChange={(event, newValue) => {
                    setField(newValue);
                  }}
                />
              ) : (
                <Typography
                  color="textPrimary"
                  variant="h5"
                >
                  {heading.field}
                </Typography>
              )}
          </Box>
          {(field && field.name === 'text_field') && (
          <Box
            mt={2}
            display="flex"
            width={500}
          >
            <Typography
              color="textPrimary"
              variant="h4"
              className={classes.fieldTitle}
            >
              Input:
            </Typography>
            <TextField
              value={value}
              onChange={(event) => setValue(event.target.value)}
              multiline
              fullWidth
              rows={6}
              variant="outlined"
            />
          </Box>
          )}
          {(field && field.name === 'select_field') && (
          <Box
            mt={2}
            display="flex"
            width={500}
          >
            <Typography
              color="textPrimary"
              variant="h4"
              className={classes.fieldTitle}
            >
              Select:
            </Typography>
            <Select
              value={value}
              onChange={(event) => { setValue(event.target.value); }}
            >
              {HEADING_OPTIONS[2].value.map((item, index) => (
                <MenuItem
                  value={item}
                  key={index}
                >
                  {item}
                </MenuItem>
              ))}
            </Select>
          </Box>
          )}
        </Box>
        <Box
          display="flex"
          flexDirection="row-reverse"
        >
          <Box
            ml={2}
          >
            <Button
              color="primary"
              variant="contained"
              size="small"
              onClick={() => { removeData(); }}
            >
              Remove
            </Button>
          </Box>
          <Box
            ml={2}
          >
            {saving
              ? (
                <Button
                  color="secondary"
                  variant="contained"
                  size="small"
                  onClick={() => { handleSave(); }}
                >
                  Save
                </Button>
              ) : (
                <Button
                  color="secondary"
                  variant="contained"
                  size="small"
                  onClick={() => { setSaving(true); }}
                >
                  Edit
                </Button>
              )}
          </Box>
          {!heading.subHeading && (
          <Box
            ml={2}
          >
            <Button
              color="secondary"
              variant="contained"
              size="small"
              onClick={() => { newSubHeading(); }}
            >
              New SubHeading
            </Button>
          </Box>
          )}
        </Box>
      </Container>
      )}
    </Box>
  );
}

HeadingDetail.propTypes = {
  data: PropTypes.object,
  updateData: PropTypes.func,
  removeData: PropTypes.func,
};

export default HeadingDetail;
