/* eslint-disable max-len */
import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {
  Box,
  Button,
  Chip,
  Divider,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails,
  Grid as GridRow,
  Input,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {
  FORMS, FORM_DESCRIPTIONS, TABLE_COLUMNS, TABLE_DATA, CHIPS
} from 'src/constants';

import { EditingState } from '@devexpress/dx-react-grid';
import {
  Grid,
  Table,
  TableHeaderRow,
  TableEditRow,
  TableEditColumn,
} from '@devexpress/dx-react-grid-material-ui';

const useStyles = makeStyles(() => ({
  root: {
    flexGrow: 1,
  },
  heading: {},
  todo: {
    backgroundColor: 'white'
  },
  panel: {
    flexWrap: 'wrap'
  },
  panelBody: {
    display: 'flex',
    justifyContent: 'space-between',
    padding: '0px'
  },
  description: {
    maxWidth: '600px',
    marginLeft: '10px',
    width: '100%',
  }
}));


function CheckItem({
  checkItem, updateItem, removeItem, headings
}) {
  const classes = useStyles();
  const [description, setDescription] = useState(checkItem.description);
  const [form, setForm] = useState(checkItem.form);
  const [heading, setHeading] = useState(headings[0]);

  const getOptionLabel = (option) => {
    for (let i = 0; i < FORM_DESCRIPTIONS.length; i++) {
      if (FORM_DESCRIPTIONS[i].title === option) {
        return FORM_DESCRIPTIONS[i].content;
      }
    }
    return FORM_DESCRIPTIONS[0].content;
  };

  const [columns, setColumns] = useState(TABLE_COLUMNS);

  const [tableData, setTableData] = useState(TABLE_DATA);

  const [tableColumnExtensions] = useState([
    { columnName: 'id', width: 60 },
  ]);

  const [editingRowIds, setEditingRowIds] = useState([]);
  const [addedRows, setAddedRows] = useState([]);
  const [rowChanges, setRowChanges] = useState({});

  const [inputValue, setInputValue] = useState('');
  const [chips, setChips] = useState(CHIPS);

  useEffect(() => {
    if (form === 'table' && checkItem.table_columns) {
      setChips(checkItem.table_columns);
    }

    if (form === 'table' && checkItem.table_data) {
      const table_data = [];
      for (let i = 0; i < checkItem.table_data.length; i++) {
        const row = {
          id: i,
        };
        for (let j = 0; j < checkItem.table_columns.length; j++) {
          const column = checkItem.table_columns[j];
          row[column] = checkItem.table_data[i][j];
        }
        table_data.push(row);
      }
      setTableData(table_data);
    }
    headings.some((item) => {
      if (item.id === checkItem.heading) {
        setHeading(item);
      }
    });
  }, []);

  useEffect(() => {
    const columnArray = [];
    for (let i = 0; i < chips.length; i++) {
      columnArray.push({ title: chips[i], name: chips[i] },);
    }
    setColumns(columnArray);
  }, [chips]);

  const getRowId = (row) => row.id;

  const handleInputChange = (event) => {
    event.persist();
    setInputValue(event.target.value);
  };

  const handleInputKeyup = (event) => {
    event.persist();

    if (event.keyCode === 13 && inputValue) {
      if (!chips.includes(inputValue)) {
        setChips((prevChips) => [...prevChips, inputValue]);
        setInputValue('');
      }
    }
  };

  const handleChipDelete = (chip) => {
    setChips((prevChips) => prevChips.filter((prevChip) => chip !== prevChip));
  };


  const changeAddedRows = (value) => {
    const initialized = value.map((row) => (Object.keys(row).length ? row : {}));
    setAddedRows(initialized);
  };


  const commitChanges = ({ added, changed, deleted }) => {
    let changedRows;
    if (added) {
      const startingAddedId = tableData.length > 0 ? tableData[tableData.length - 1].id + 1 : 0;
      changedRows = [
        ...tableData,
        ...added.map((row, index) => ({
          id: startingAddedId + index,
          ...row,
        })),
      ];
    }
    if (changed) {
      changedRows = tableData.map((row) => (changed[row.id] ? { ...row, ...changed[row.id] } : row));
    }
    if (deleted) {
      const deletedSet = new Set(deleted);
      changedRows = tableData.filter((row) => !deletedSet.has(row.id));
    }
    setTableData(changedRows);
  };

  const onUpdateItem = () => {
    checkItem.description = description;
    checkItem.form = form;
    checkItem.heading = heading.id;
    checkItem.expanded = false;
    if (form === 'table') {
      if (chips) {
        checkItem.table_columns = chips;
      }
      if (tableData) {
        const table_data = [];
        for (let i = 0; i < tableData.length; i++) {
          const row = [];
          for (let j = 0; j < chips.length; j++) {
            const column = chips[j];
            const newColumn = tableData[i][column] || '';
            row.push(newColumn);
          }
          table_data.push(row);
        }
        checkItem.table_data = table_data;
      }
    }
    updateItem(checkItem);
  };

  const onRemoveItem = () => {
    removeItem(checkItem._id);
  };

  return (
    <>
      <ExpansionPanel
        defaultExpanded={checkItem.expanded}
      >
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
        >
          <Typography className={classes.heading}>{description}</Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails
          className={classes.panel}
        >
          <GridRow
            xs={12}
            item
            className={classes.panelBody}
          >
            <Autocomplete
              value={heading}
              options={headings}
              getOptionLabel={(option) => option.label}
              style={{ width: 300 }}
              renderInput={(params) => <TextField {...params} label="Heading" variant="outlined" />}
              onChange={(event, value) => {
                setHeading(value);
              }}
            />
            <Autocomplete
              value={form}
              options={FORMS}
              getOptionLabel={(option) => getOptionLabel(option)}
              style={{ width: 300 }}
              renderInput={(params) => <TextField {...params} label="Form Type" variant="outlined" />}
              onChange={(event, value) => {
                setForm(value);
              }}
            />
            <TextField
              placeholder="Description"
              variant="outlined"
              label="Description"
              className={classes.description}
              defaultValue={description}
              onChange={(e) => {
                setDescription(e.target.value);
              }}
            />
            <Button onClick={() => { onUpdateItem(); }}>
              Update
            </Button>
            <Button onClick={() => { onRemoveItem(); }}>
              Remove
            </Button>
          </GridRow>
          {(form === 'table')
          && (
            <>
              <>
                <Box
                  p={1}
                  display="flex"
                  alignItems="center"
                >
                  <Input
                    disableUnderline
                    fullWidth
                    className={classes.searchInput}
                    onChange={handleInputChange}
                    onKeyUp={handleInputKeyup}
                    placeholder="Enter a keyword"
                    value={inputValue}
                  />
                </Box>
                <Divider />
                <Box
                  p={1}
                  display="flex"
                  alignItems="center"
                  flexWrap="wrap"
                >
                  {chips.map((chip) => (
                    <Chip
                      className={classes.chip}
                      key={chip}
                      label={chip}
                      onDelete={() => handleChipDelete(chip)}
                    />
                  ))}
                </Box>
                <Divider />
              </>
              <>
                <Grid
                  rows={tableData}
                  columns={columns}
                  getRowId={getRowId}
                >
                  <EditingState
                    editingRowIds={editingRowIds}
                    onEditingRowIdsChange={setEditingRowIds}
                    rowChanges={rowChanges}
                    onRowChangesChange={setRowChanges}
                    addedRows={addedRows}
                    onAddedRowsChange={changeAddedRows}
                    onCommitChanges={commitChanges}
                  />
                  <Table
                    columnExtensions={tableColumnExtensions}
                  />
                  <TableHeaderRow />
                  <TableEditRow />
                  <TableEditColumn
                    showAddCommand={!addedRows.length}
                    showEditCommand
                    showDeleteCommand
                  />
                </Grid>
              </>
            </>
          )}
        </ExpansionPanelDetails>
      </ExpansionPanel>
    </>
  );
}

CheckItem.propTypes = {
  checkItem: PropTypes.object,
  updateItem: PropTypes.func,
  removeItem: PropTypes.func,
  headings: PropTypes.array,
};

export default CheckItem;
