import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  Card,
  Box,
  Grid,
  Typography,
  makeStyles
} from '@material-ui/core';
import TreeView from '@material-ui/lab/TreeView';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem';
import HeadingDetail from './HeadingDetail';

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
    maxWidth: 1024,
    backgroundColor: 'white',
  },
  container: {

  },
  panel: {
    minHeight: 500,
  },
  treeView: {
    marginTop: 20,
  },
  treeItem: {
    witdh: 300,
  }
});

function Heading({ className, headings, updateHeadings }) {
  const classes = useStyles();
  const [expanded, setExpanded] = useState([]);
  const [selected, setSelected] = useState(null);
  const [selHeading, setSelHeading] = useState(null);
  const handleToggle = (event, nodeIds) => {
    setExpanded(nodeIds);
  };

  const handleSelect = (event, nodeId) => {
    setSelected(nodeId);
    headings.some((heading) => {
      if (heading.id === nodeId) {
        setSelHeading(heading);
      } else if (heading.children) {
        heading.children.some((child) => {
          if (child.id === nodeId) {
            setSelHeading({ ...child, subHeading: true });
          }
        });
      }
    });
  };

  const updateHeading = (data) => {
    const newHeadings = [...headings];
    newHeadings.some((heading, index) => {
      if (heading.id === data.id) {
        newHeadings[index] = data;
        updateHeadings(newHeadings);
      } else if (heading.children) {
        heading.children.some((child, indexChild) => {
          if (child.id === data.id) {
            newHeadings[index].children[indexChild] = data;
            updateHeadings(newHeadings);
          }
        });
      }
    });
    setSelHeading(data);
  };

  const removeHeading = () => {
    const newHeadings = [...headings];
    newHeadings.some((heading, index) => {
      if (heading.id === selHeading.id) {
        newHeadings.splice(index, 1);
        updateHeadings(newHeadings);
      } else if (heading.children) {
        heading.children.some((child, indexChild) => {
          if (child.id === selHeading.id) {
            newHeadings[index].children.splice(indexChild, 1);
            updateHeadings(newHeadings);
          }
        });
      }
    });
    setSelHeading(null);
  };

  return (
    <Box
      className={clsx(classes.root, className)}
    >
      <Box
        className={classes.panel}
        mt={3}
        ml={1}
        display="flex"
      >
        <Grid
          variant="outlined"
        >
          <Typography
            variant="h5"
          >
            Headings
          </Typography>
          <TreeView
            className={classes.treeView}
            defaultCollapseIcon={<ExpandMoreIcon />}
            defaultExpandIcon={<ChevronRightIcon />}
            expanded={expanded}
            selected={selected}
            onNodeToggle={handleToggle}
            onNodeSelect={handleSelect}
          >
            {headings.map((heading) => (
              <TreeItem
                className={classes.treeItem}
                key={heading.id}
                nodeId={heading.id}
                label={heading.label}
              >
                { heading.children && (heading.children.map((subheading) => (
                  <TreeItem
                    className={classes.treeItem}
                    key={subheading.id}
                    nodeId={subheading.id}
                    label={subheading.label}
                  />
                )))}
              </TreeItem>
            ))}
          </TreeView>
        </Grid>
        {selHeading && (
          <Grid>
            <HeadingDetail
              data={selHeading}
              updateData={updateHeading}
              removeData={removeHeading}
            />
          </Grid>
        )}
      </Box>
    </Box>
  );
}

Heading.propTypes = {
  className: PropTypes.string,
  headings: PropTypes.array,
  updateHeadings: PropTypes.func,
};


export default Heading;
