import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useSnackbar } from 'notistack';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { v4 as uuidv4 } from 'uuid';
import axios from 'src/utils/axios';
import {
  Box,
  TextField,
  Typography,
  Grid,
  Button,
  makeStyles
} from '@material-ui/core';
import { getReports } from 'src/actions/reportActions';
import { SET_HEADINGS } from 'src/actions/serviceActions';
import { API_BASE_URL } from 'src/config';
import { SERVICE } from 'src/config/endpoint';
import HeadingDetails from './HeadingDetails';

const useStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: 30,
    paddingRight: 30,
  },
  container: {
    minHeight: 500,
  },
  formButton: {
    marginLeft: theme.spacing(2)
  }
}));

function ReportTemplate({
  className,
  serviceHeadings,
  serviceId,
}) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [headings, setHeadings] = useState(serviceHeadings);
  const { enqueueSnackbar } = useSnackbar();

  useEffect(() => {
    dispatch(getReports());
  }, []);

  // const handleChangeTemplate = (event) => {
  //   const index = event.target.value;
  //   setSelTemplate(reports[index]);
  // };

  const handleCreateHeading = () => {
    setHeadings([...headings, { id: uuidv4(), label: 'New Heading' }]);
  };

  const handleUpdate = () => {
    axios.put(`${API_BASE_URL}/${SERVICE.UPDATE}/${serviceId}`, {
      headings,
    })
      .then(() => {
        enqueueSnackbar('Report updated', {
          variant: 'success',
        });
        dispatch({
          type: SET_HEADINGS,
          payload: headings,
        });
      })
      .catch((error) => {
        console.log('error', error);
      });
  };

  return (
    <Box mt={2}>
      <Box
        className={classes.container}
      >
        {/* <Grid>
            <TextField
              fullWidth
              name="template"
              onChange={handleChangeTemplate}
              select
              SelectProps={{ native: true }}
              variant="outlined"
            >
              {reports.map((template, index) => (
                <option
                  key={template._id}
                  value={index}
                >
                  {`${template.title}`}
                </option>
              ))}
            </TextField>
          </Grid>
          {selTemplate && (
          <Grid>
            <HeadingItem
              headings={selTemplate.headings}
            />
          </Grid>
          )} */}

        <HeadingDetails
          headings={headings}
          updateHeadings={(newHeadings) => { setHeadings(newHeadings); }}
        />
        <Box
          display="flex"
          flexDirection="row-reverse"
          maxWidth={1024}
          mt={2}
        >
          <Button
            className={classes.formButton}
            color="secondary"
            variant="contained"
            size="small"
            onClick={handleUpdate}
          >
            Update
          </Button>
          <Button
            className={classes.formButton}
            color="secondary"
            variant="contained"
            size="small"
            onClick={handleCreateHeading}
          >
            Create Heading
          </Button>
        </Box>
      </Box>
    </Box>
  );
}

ReportTemplate.propTypes = {
  className: PropTypes.string,
  serviceHeadings: PropTypes.array,
  serviceId: PropTypes.string,
};

export default ReportTemplate;
