import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import axios from 'src/utils/axios';
import { useSnackbar } from 'notistack';
import { v4 as uuidv4 } from 'uuid';
import {
  ReactSortable, Sortable, MultiDrag, Swap
} from 'react-sortablejs';
import {
  Box,
  Button,
  Container,
  makeStyles
} from '@material-ui/core';
import CheckItem from 'src/components/CheckItem';
import { API_BASE_URL } from 'src/config';
import { SERVICE } from 'src/config/endpoint';
import { SET_SERVICE } from 'src/actions/serviceActions';

Sortable.mount(new MultiDrag(), new Swap());

const useStyles = makeStyles((theme) => ({
  root: {},
  heading: {},
  todo: {
    backgroundColor: 'white'
  },
  description: {
    maxWidth: '400px',
    marginLeft: '20px',
    width: '100%',
  },
  actionButton: {
    marginLeft: theme.spacing(2)
  }
}));

function CheckList({
  className, checkItems, headings, serviceId
}) {
  const classes = useStyles();
  const [checkList, setCheckList] = useState(checkItems);
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  const updateCheckList = () => {
    axios.put(`${API_BASE_URL}/${SERVICE.UPDATE}/${serviceId}`, { check_list: checkList })
      .then(() => {
        dispatch({
          type: SET_SERVICE,
          payload: {
            check_list: checkList,
          }
        });
        enqueueSnackbar('Service updated', {
          variant: 'success'
        });
      })
      .catch((error) => {
        console.log('service update error', error);
      });
  };

  const removeCheckItem = (id) => {
    const newCheckList = [...checkList];
    newCheckList.some((check_item, index) => {
      if (check_item._id === id) {
        newCheckList.splice(index, 1);
        setCheckList(newCheckList);
      }
    });
  };

  return (
    <Container>
      {checkList && (
      <ReactSortable
        multiDrag
        swap
        list={checkList}
        setList={setCheckList}
        className={clsx(classes.root, className)}
      >
        {checkList.map((todo, index) => (
          <CheckItem
            key={todo._id}
            checkItem={todo}
            headings={headings}
            updateItem={(newTodo) => {
              checkList[index] = newTodo;
              setCheckList(checkList);
            }}
            removeItem={(id) => { removeCheckItem(id); }}
          />
        ))}
      </ReactSortable>
      )}
      <Box
        display="flex"
        flexDirection="row-reverse"
        mt={2}
      >
        <Button
          className={classes.actionButton}
          color="secondary"
          variant="contained"
          onClick={() => { updateCheckList(); }}
        >
          Update
        </Button>
        <Button
          className={classes.actionButton}
          color="secondary"
          variant="contained"
          onClick={() => {
            if (checkList) {
              setCheckList([...checkList, {
                _id: uuidv4(),
                form: '',
                description: '',
                expanded: true
              }]);
            } else {
              setCheckList([{
                _id: uuidv4(),
                form: 'input',
                description: '',
                expanded: true
              }]);
            }
          }}
        >
          Add To Do
        </Button>
      </Box>
    </Container>
  );
}

CheckList.propTypes = {
  className: PropTypes.string,
  serviceId: PropTypes.string.isRequired,
  checkItems: PropTypes.array,
  headings: PropTypes.array,
};

export default CheckList;
