import React, { useState, useRef } from 'react';
import { useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  Box,
  Card,
  CardMedia,
  Button,
  SvgIcon,
  TextField,
  Typography,
  colors,
  makeStyles,
  TextareaAutosize,
} from '@material-ui/core';
import { Users as UsersIcon } from 'react-feather';
import { setService } from 'src/actions/serviceActions';

const useStyles = makeStyles((theme) => ({
  root: {},
  media: {
    height: 250,
    backgroundColor: theme.palette.background.dark
  },
  likedButton: {
    color: colors.red[600]
  },
  subscribersIcon: {
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(1)
  },
  editBox: {
    justifyContent: 'space-between'
  },
  descriptionArea: {
    height: 150,
    maxWidth: 700,
    fontSize: '1.2em',
    fontWeight: 400,
    fontFamily: 'inherit',
    color: 'currentColor',
    width: '100%',
  }
}));

function ProjectCard({
  className, project, ...rest
}) {
  const pickerInput = useRef();
  const classes = useStyles();
  const [saving, setSaving] = useState(false);
  const [file, setFile] = useState(null);
  const [cover, setCover] = useState(project.cover);
  const [title, setTitle] = useState(project.title);
  const [description, setDescription] = useState(project.description);
  const dispatch = useDispatch();

  const updateOverview = () => {
    setSaving(false);
    const data = {
      service: {
        title,
        description,
      },
      file,
      serviceId: project.id,
    };
    dispatch(setService(data));
  };

  const _onFileInputHandler = (event) => {
    event.preventDefault();
    const newFile = event.target.files[0];
    const url = URL.createObjectURL(newFile);
    setCover(url);
    setFile(newFile);
    setSaving(true);
  };

  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Box p={3}>
        <Box>
          <input
            ref={pickerInput}
            type="file"
            style={{
              display: 'none'
            }}
            onChange={(e) => { _onFileInputHandler(e); }}
          />
          <Box
            onClick={() => {
              pickerInput.current.click();
            }}
          >
            { cover ? (
              <CardMedia
                className={classes.media}
                image={cover}
              />
            ) : (
              <CardMedia
                className={classes.media}
                image={cover}
              />
            )}
          </Box>
        </Box>
        <Box
          display="flex"
          alignItems="center"
          mt={2}
        >
          <Box px={1}>
            {saving
              ? (
                <TextField
                  onChange={(e) => { setTitle(e.target.value); }}
                  defaultValue={project.title}
                />
              ) : (
                <Typography
                  color="textPrimary"
                  variant="h5"
                >
                  {project.title}
                </Typography>
              )}
          </Box>
        </Box>
      </Box>
      <Box
        pb={2}
        px={4}
        display="flex"
      >
        {saving
          ? (
            <TextareaAutosize
              className={classes.descriptionArea}
              onChange={(e) => { setDescription(e.target.value); }}
              defaultValue={project.description}
              rowsMin={3}
            />
          ) : (
            <Typography
              color="textSecondary"
              variant="body2"
            >
              {project.description}
            </Typography>
          )}
      </Box>
      <Box
        py={2}
        px={3}
        display="flex"
        className={classes.editBox}
      >
        <Box
          display="flex"
        >
          <SvgIcon
            fontSize="small"
            color="action"
            className={classes.subscribersIcon}
          >
            <UsersIcon />
          </SvgIcon>
          <Typography
            variant="subtitle2"
            color="textSecondary"
          >
            {project.customers || 0}
          </Typography>
        </Box>
        {saving ? <Button onClick={() => { updateOverview(); }}>Save</Button>
          : <Button onClick={() => { setSaving(true); }}>Edit</Button>}
      </Box>
    </Card>
  );
}

ProjectCard.propTypes = {
  className: PropTypes.string,
  project: PropTypes.object.isRequired,
  setProject: PropTypes.func,
  setFile: PropTypes.func,
};

export default ProjectCard;
