/* eslint-disable react/destructuring-assignment */
import React, {
  useState,
  useEffect
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  Box,
  Container,
  Divider,
  Tabs,
  Tab,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import { getService } from 'src/actions/serviceActions';
import Overview from './Overview';
import CheckList from './CheckList';
import ReportTemplate from './ReportTemplate';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

function ProjectDetailsView(props) {
  const classes = useStyles();
  const [currentTab, setCurrentTab] = useState('overview');
  const dispatch = useDispatch();
  // eslint-disable-next-line react/prop-types
  const { serviceId } = props.match.params;
  const { service } = useSelector((state) => state.service);
  const tabs = [
    { value: 'overview', label: 'Overview' },
    { value: 'checklist', label: 'Check List' },
    { value: 'report', label: 'Report' },
  ];

  const handleTabsChange = (event, value) => {
    setCurrentTab(value);
  };

  useEffect(() => {
    dispatch(getService(serviceId));
  }, [dispatch]);

  if (!service) {
    return null;
  }

  return (
    <Page
      className={classes.root}
      title="Project Details"
    >
      <Container maxWidth="lg">
        {/* <Header project={service} /> */}
        <Box mt={3}>
          <Tabs
            onChange={handleTabsChange}
            scrollButtons="auto"
            textColor="secondary"
            value={currentTab}
            variant="scrollable"
          >
            {tabs.map((tab) => (
              <Tab
                key={tab.value}
                label={tab.label}
                value={tab.value}
              />
            ))}
          </Tabs>
        </Box>
        <Divider />
        <Box mt={3}>
          {currentTab === 'overview' && <Overview service={service} />}
          {currentTab === 'checklist' && (
          <CheckList
            serviceId={service._id}
            checkItems={service.check_list}
            headings={service.headings}
          />
          )}
          {currentTab === 'report' && (
          <ReportTemplate
            serviceId={service._id}
            serviceHeadings={service.headings}
          />
          )}
        </Box>
      </Container>
    </Page>
  );
}

export default ProjectDetailsView;
